import GuessPeg from "./GuessPeg"
import ColorPicker from "./ColorPicker"

export default function PegGridRow(props) {
  return(
    <tr>
      <td>{props.blackPegs[props.rowNum]}B<br />{props.whitePegs[props.rowNum]}W</td>
      <td><GuessPeg handleGuessPegColorChange={props.handleGuessPegColorChange} guessPegColors={props.guessPegColors} rowNum={props.rowNum} colNum="0" /></td>
      <td><GuessPeg handleGuessPegColorChange={props.handleGuessPegColorChange} guessPegColors={props.guessPegColors} rowNum={props.rowNum} colNum="1" /></td>
      <td><GuessPeg handleGuessPegColorChange={props.handleGuessPegColorChange} guessPegColors={props.guessPegColors} rowNum={props.rowNum} colNum="2" /></td>
      <td><GuessPeg handleGuessPegColorChange={props.handleGuessPegColorChange} guessPegColors={props.guessPegColors} rowNum={props.rowNum} colNum="3" /></td>
      <td><GuessPeg handleGuessPegColorChange={props.handleGuessPegColorChange} guessPegColors={props.guessPegColors} rowNum={props.rowNum} colNum="4" /></td>
      <td><ColorPicker handleColorPick={props.handleColorPick} color={props.color} /></td>
      </tr>
  )
}
